import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';

function Tv() {
  const [tvs, setTvs] = useState([]);
  const [pageNum, setPageNum] = useState(1);
  const [searchQuery, setSearchQuery] = useState('');

  //  API 
  const searchApiUrl = 'https://api.themoviedb.org/3/search/tv';
  //    API 
  const popularApiUrl = 'https://api.themoviedb.org/3/tv/popular';

  // useHistory   history  
  const navigate = useNavigate();

  useEffect(() => {
    let apiUrl = '';

    if (searchQuery) {
      apiUrl = `${searchApiUrl}?api_key=f89a6c1f22aca3858a4ae7aef10de967&query=${searchQuery}&page=${pageNum}`;
    } else {
      apiUrl = `${popularApiUrl}?api_key=f89a6c1f22aca3858a4ae7aef10de967&page=${pageNum}`;
    }

    axios.get(apiUrl)
      .then(response => {
        if (pageNum === 1) {
          setTvs(response.data.results);
        } else {
          setTvs(prevTVs => [...prevTVs, ...response.data.results]);
        }
      })
      .catch(error => {
        console.error('Error fetching data:', error);
      });

    //    URL 
    navigate(`?search=${searchQuery}`);
  }, [pageNum, searchQuery, navigate]);

  function loadMore() {
    setPageNum(prevPageNum => prevPageNum + 1);
  }

  function handleSearchInputChange(event) {
    setSearchQuery(event.target.value);
  }

  function handleSearch() {
    //          
    setPageNum(1);
  }

  return (
    <div className='movie_whole'>
      <div className='movie_title'>Tv Series</div>
      <div className='movie_consear'>
        <div className='search'>
          <input
            type='text'
            placeholder="Enter keyword"
            value={searchQuery}
            onChange={handleSearchInputChange}
          />
          <button onClick={handleSearch}>Search</button>
        </div>
        <div className='movie_con'>
          <div className="upcoming">
            {tvs.map(tv => (
              <div className='aa' key={tv.id}>
                <div className="upcoming_con">
                  <Link to={`/tv/${tv.id}`}>
                    <div className="hoverred">
                      <div className="img_whole">
                        <div className='back'
                          style={{
                            backgroundImage: `url(https://image.tmdb.org/t/p/original/${tv.poster_path})`,
                            backgroundPosition: '50%',
                            backgroundSize: 'cover',
                            backgroundRepeat: 'no-repeat',
                          }}>
                        </div>
                      </div>
                      <div className="hoverbutton">
                        <div>
                          <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M7 6v12l10-6z"></path></svg>
                        </div>
                      </div>
                    </div>
                    <h4>{tv.name}</h4>
                  </Link>
                </div>
              </div>
            ))}
          </div>
          <div className='more'>
            <p onClick={loadMore}>Load more</p>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Tv;
